import React, { useState, useCallback, useMemo, memo } from 'react';
import { Row, Col, Space, Menu, Divider, Card, Segmented } from 'antd';
import Icon, {
  DownOutlined,
  PlusOutlined,
  EditOutlined,
  DeleteOutlined,
  SyncOutlined,
  PrinterOutlined,
  QuestionOutlined,
  InfoOutlined,
  SettingOutlined,
  ExclamationCircleOutlined,
  SearchOutlined,
  RollbackOutlined,
  ClearOutlined,
  SaveOutlined,
  FastForwardFilled,
  WhatsAppOutlined,
  ImportOutlined,
  ExportOutlined,
} from '@ant-design/icons';
import WayButton from './waybutton';
import type { WayProSearchProps } from './wayprosearch';
import WayProSearch from './wayprosearch';
import type { CommandAttribute, SearchWhere, WayFieldAttribute } from '../way';

export interface WayComvtpSearchProps {
  show?: boolean;
  autoSearch?: boolean;
  resetKey?: number;
  fields?: WayFieldAttribute[];
  onSearch?: (w: SearchWhere | SearchWhere[]) => void;
  onValueChange?: (w: SearchWhere[]) => void;
}

export interface WayToolbarProps {
  attrs?: CommandAttribute[];
  searchShow?: false | WayProSearchProps;
  comvtpSearchShow?: false | WayComvtpSearchProps;
  commandShow?: boolean;
  helpShow?: false | WayHelpProps;
  viewOredit?: string | 'view' | 'edit';
  iscircle?: boolean;
  isselectrow?: boolean;
  selectcount?: number;
  isclosecard?: boolean;
  onClick?: (name: string, command: CommandAttribute) => void;
  onCommandDisabled?: (command: CommandAttribute) => boolean;
}
interface WayHelpProps {
  isprint?: boolean;
  isset?: boolean;
  ishelp?: boolean;
  isabout?: boolean;
  wvh?: DefultViewHelp;
}
class DefultViewHelp {
  printText: string = '打印';
  setText: string = '设置';
  helpText: string = '帮助';
  aboutText: string = '关于';
  onPrint(name: string): void { }
  onSet(name: string): void { }
  onHelp(name: string): void { }
  onAbout(name: string): void { }
}
const WayToolbar: React.FC<WayToolbarProps> = (props) => {
  const primaryKeys = ['add', 'create'];
  const dangerKeys = ['remove', 'delete'];
  const editKeys = ['update', 'edit'];
  const importKeys = ['ImportData', 'import'];
  const exportKeys = ['ExportData', 'export'];
  function initcommands() {
    const mianButtons: CommandAttribute[] = [];
    const splitButtons: Map<string, CommandAttribute[]> = new Map();
    props.attrs?.forEach((attr) => {
      if (attr.issplit && attr.splitname != undefined) {
        if (splitButtons.has(attr.splitname)) {
          const items = splitButtons.get(attr.splitname);
          items?.push(attr);
        } else {
          splitButtons.set(attr.splitname, [attr]);
        }
      } else {
        mianButtons.push(attr);
      }
    });
    return (
      <Space wrap>
        {mianButtons.map((attr) => {
          if (splitButtons.has(attr.command)) {
            const menu = (
              <Menu>
                {splitButtons.get(attr.command)?.map((sattr, i) => {
                  return (
                    <Menu.Item key={i}>
                      <WayButton
                        key={i}
                        {...attrToButtonProps(sattr)}
                        onClick={() => onClick(sattr.command, sattr)}
                        toolbar={props}
                      />
                    </Menu.Item>
                  );
                })}
              </Menu>
            );
            return (
              <WayButton
                {...attrToButtonProps(attr)}
                icon={<DownOutlined />}
                ismenu={true}
                menu={menu}
                onClick={() => onClick(attr.command, attr)}
                toolbar={props}
              />
            );
          } else {
            return (
              <WayButton
                {...attrToButtonProps(attr)}
                onClick={() => onClick(attr.command, attr)}
                toolbar={props}
              />
            );
          }
        })}
      </Space>
    );
  }
  function getseletrowdisabled(attr: CommandAttribute) {
    if (!props.isselectrow) return false;
    if (props.onCommandDisabled != undefined) return props.onCommandDisabled(attr);
    if (props.selectcount == 1 && attr.isselectrow) return false;
    if (
      props.selectcount != undefined &&
      props.selectcount > 1 &&
      attr.isselectrow &&
      attr.selectmultiple
    )
      return false;
    return attr.isselectrow;
  }
  function inithelps() {
    if (props.helpShow != undefined && props.helpShow != false) {
      const [whp, setWhp] = useState({
        isprint: props.helpShow?.isprint ?? false,
        isset: props.helpShow?.isset ?? false,
        ishelp: props.helpShow?.ishelp ?? false,
        isabout: props.helpShow?.isabout ?? false,
        wvh: props.helpShow?.wvh ?? new DefultViewHelp(),
      });
      return <Space wrap>{showhelp(whp)}</Space>;
    }
  }
  function showhelp(whp: WayHelpProps) {
    const items = [];
    if (whp.isprint) {
      items.push(
        <WayButton
          title={whp.wvh?.printText}
          name={'print'}
          shape={'circle'}
          icon={<PrinterOutlined />}
          onClick={onClick}
        />,
      );
    }
    if (whp.isset) {
      items.push(
        <WayButton
          title={whp.wvh?.setText}
          name={'set'}
          shape={'circle'}
          icon={<SettingOutlined />}
          onClick={onClick}
        />,
      );
    }
    if (whp.ishelp) {
      items.push(
        <WayButton
          title={whp.wvh?.helpText}
          name={'help'}
          shape={'circle'}
          icon={<QuestionOutlined />}
          onClick={onClick}
        />,
      );
    }
    if (whp.isabout) {
      items.push(
        <WayButton
          title={whp.wvh?.aboutText}
          name={'about'}
          shape={'circle'}
          icon={<InfoOutlined />}
          onClick={onClick}
        />,
      );
    }
    if (items.length > 0) {
      items.unshift(<Divider type="vertical" />);
    }
    return items.map((h) => {
      return h;
    });
  }
  function initsearch() {
    if (props.searchShow != undefined && props.searchShow != false) {
      return <WayProSearch {...props.searchShow} />;
    }
  }
  function initComvtpSearch() {
    if (props.comvtpSearchShow == undefined || props.comvtpSearchShow == false) return null;
    if (!(props.comvtpSearchShow.show ?? true)) return null;
    const autoSearch = props.comvtpSearchShow.autoSearch ?? false;
    const comvtpFields =
      props.comvtpSearchShow.fields?.filter(
        (field) => (field.issearch ?? true) && Boolean(field.comvtp) && field.comvtp?.items,
      ) ?? [];
    const firstComvtpSearchField =
      comvtpFields.find((field) => field.showInComvtp) ?? comvtpFields[0];
    if (!firstComvtpSearchField) return null;
    const searchName =
      firstComvtpSearchField.field ?? firstComvtpSearchField.porpfield ?? firstComvtpSearchField.title ?? '';
    const allValue = '__all__';
    const comvtpItems = firstComvtpSearchField.comvtp?.items;
    const options: Array<{ label: string; value: string | number }> = [{ label: '全部', value: allValue }];
    if (comvtpItems instanceof Map) {
      comvtpItems.forEach((label, value) => {
        options.push({ label: String(label), value });
      });
    } else if (Array.isArray(comvtpItems)) {
      comvtpItems.forEach((item: any) => {
        if (Array.isArray(item) && item.length >= 2) {
          options.push({ label: String(item[1]), value: item[0] as string | number });
        }
      });
    } else if (comvtpItems && typeof comvtpItems === 'object') {
      Object.entries(comvtpItems as Record<string, any>).forEach(([value, label]) => {
        const parsedValue = /^\d+$/.test(value) ? Number(value) : value;
        options.push({ label: String(label), value: parsedValue });
      });
    }
    return (
      <div style={{ minWidth: 260 }}>
        <Segmented
          key={`comvtp-${props.comvtpSearchShow.resetKey ?? 0}`}
          defaultValue={allValue}
          options={options}
          onChange={(value) => {
            if (props.comvtpSearchShow == undefined || props.comvtpSearchShow == false) return;
            const whereList =
              value === allValue
                ? []
                : [
                  {
                    name: searchName,
                    symbol: '=',
                    value: String(value),
                  },
                ];
            if (props.comvtpSearchShow.onValueChange) {
              props.comvtpSearchShow.onValueChange(whereList);
            }
            if (!autoSearch) return;
            if (props.comvtpSearchShow.onSearch) {
              props.comvtpSearchShow.onSearch(whereList);
            }
          }}
        />
      </div>
    );
  }
  const onClick = (name: any, command: CommandAttribute) => {
    if (props.onClick != undefined) props.onClick(name, command);
  };
  function attrToButtonProps(attr: CommandAttribute) {
    let prop = {
      name: attr.command,
      title: attr.title,
      text: attr.name,
      danger: false,
      block: false,
      type: 'default',
      size: 'default',
      shape: '',
      loading: false,
      icon: '',
      ghost: false,
      disabled: false,
    };
    prop.disabled = getseletrowdisabled(attr) ?? false;
    prop = setTypeAndIcon(prop);
    if (props.iscircle) {
      prop.shape = 'circle';
      prop.title = prop.text;
      prop.text = '';
    }
    return prop;
  }
  function setTypeAndIcon(prop: any) {
    if (primaryKeys.includes(prop.name)) {
      prop.type = 'primary';
      prop.icon = <Icon component={PlusOutlined} />;
    }
    if (dangerKeys.includes(prop.name)) {
      prop.type = 'primary';
      prop.danger = true;
      prop.icon = <DeleteOutlined />;
    }
    if (editKeys.includes(prop.name)) {
      prop.icon = <EditOutlined />;
    }
    if (importKeys.includes(prop.name)) {
      prop.icon = <ImportOutlined />;
    }
    if (exportKeys.includes(prop.name)) {
      prop.icon = <ExportOutlined />;
    }
    if (prop.icon == '') {
      prop.icon = <SyncOutlined />;
    }
    return prop;
  }

  function render() {
    const itmes = [];
    if (props.searchShow != undefined && props.searchShow != false) {
      itmes.push(
        <Row>
          <Col span={24}>{initsearch()}</Col>
        </Row>,
      );
    }
    if (props.commandShow != undefined && props.commandShow != false) {
      const comvtpSearch = initComvtpSearch();
      if (comvtpSearch) {
        itmes.push(
          <Row justify="space-between" align="middle" wrap={false} gutter={24}>
            <Col span={12} style={{ paddingInline: '0', overflowX: 'auto', scrollbarWidth: 'thin', scrollbarColor: 'rgba(255,255,255,0.25) rgba(255,255,255,0.12)' }}>{comvtpSearch}</Col>
            <Col flex="none">{initcommands()}</Col>
          </Row>,
        );
      } else {
        itmes.push(
          <Row justify="end">
            <Col>{initcommands()}</Col>
          </Row>,
        );
      }
    }
    if (props.helpShow != undefined && props.helpShow != false) {
      itmes.push(
        <Row justify="end">
          <Col>{inithelps()}</Col>
        </Row>,
      );
    }
    if (props.isclosecard) {
      return (
        <Space direction="vertical" size={8} style={{ width: '100%' }}>
          {itmes}
        </Space>
      );
    } else {
      return (
        <Card>
          <Space direction="vertical" size={8} style={{ width: '100%' }}>
            {itmes}
          </Space>
        </Card>
      );
    }
  }
  return render();
};

export default memo(WayToolbar);
