import React, { useEffect, useMemo, useState } from 'react';
import WayTextBox from '../WayTextBox';
import { Button, Col, Dropdown, Flex, Row, Space } from 'antd';
import { DownOutlined, RollbackOutlined, SearchOutlined } from '@ant-design/icons';
import { SearchItem, SearchWhere, TableData, WayFieldAttribute } from '../way';
import { isArray } from 'lodash';
import { TypeDataToAny } from '../waymodel';

export interface WayProSearchProps {
  fields?: WayFieldAttribute[];
  onSearch: (w: SearchWhere | SearchWhere[]) => void;
  onReset?: () => void;
  resetKey?: number;
  onSearchData?: (item: SearchItem, callback: (data: TableData) => void) => void;
}
const WayProSearch: React.FC<WayProSearchProps> = (props) => {
  const symbolType = new Map<string, object[]>();
  symbolType.set('string', [
    { label: '等于', value: '=' },
    { label: '包含', value: 'like' },
    { label: '为空', value: 'isnull' },
    { label: '不为空', value: 'isnotnull' },
    { label: '左匹配', value: 'left' },
    { label: '右匹配', value: 'right' },
  ]);
  symbolType.set('int', [
    { label: '等于', value: '=' },
    { label: '大于', value: '>' },
    { label: '小于', value: '<' },
  ]);
  symbolType.set('datetime', [
    { label: '范围', value: 'date' },
    { label: '周', value: 'week' },
    { label: '月', value: 'month' },
    { label: '季', value: 'quarter' },
    { label: '年', value: 'year' },
  ]);

  const searchFields = useMemo(
    () => props.fields?.filter((item) => item.issearch) ?? [],
    [props.fields],
  );
  const [fieldValues, setFieldValues] = useState<Record<string, any>>({});
  const [fieldSymbols, setFieldSymbols] = useState<Record<string, string>>({});
  const [fieldPickers, setFieldPickers] = useState<Record<string, string | undefined>>({});

  useEffect(() => {
    const keys = new Set(
      searchFields
        .map((item) => item.field ?? item.title)
        .filter((item): item is string => Boolean(item)),
    );
    setFieldValues((prev) => {
      const next: Record<string, any> = {};
      keys.forEach((key) => {
        if (key in prev) next[key] = prev[key];
      });
      return next;
    });
    setFieldSymbols((prev) => {
      const next: Record<string, string> = {};
      keys.forEach((key) => {
        if (key in prev) next[key] = prev[key];
      });
      return next;
    });
    setFieldPickers((prev) => {
      const next: Record<string, string | undefined> = {};
      keys.forEach((key) => {
        if (key in prev) next[key] = prev[key];
      });
      return next;
    });
  }, [searchFields]);

  useEffect(() => {
    setFieldValues({});
    setFieldSymbols({});
    setFieldPickers({});
  }, [props.resetKey]);

  function getSearchName(field: WayFieldAttribute) {
    return field.field ?? field.porpfield ?? field.title ?? '';
  }

  function getFieldKey(field: WayFieldAttribute) {
    return field.field ?? field.title ?? '';
  }

  function getFieldSymbolOptions(field: WayFieldAttribute) {
    if (field.foreign && field.foreign.isfkey) return [] as { label: string; value: string }[];
    let child = field.searchsymbol ?? symbolType.get(field.type);
    if (field.type == 'datetime') {
      child = symbolType.get(field.type);
    }
    if (field.type == 'decimal') {
      child = symbolType.get('int');
    }
    return (child ?? []) as { label: string; value: string }[];
  }

  function getDefaultSymbol(field: WayFieldAttribute) {
    const options = getFieldSymbolOptions(field);
    if (options.length > 0) return options[0].value;
    return '';
  }

  function renderFieldItem(field: WayFieldAttribute) {
    const fieldKey = getFieldKey(field);
    if (!fieldKey) return null;
    const options = getFieldSymbolOptions(field);
    const symbol = fieldSymbols[fieldKey] ?? getDefaultSymbol(field);
    const picker = fieldPickers[fieldKey];
    return (
      <div
        key={fieldKey}
        style={{
          display: 'flex',
          alignItems: 'center',
          gap: 8,
          width: '100%',
        }}
      >
        {options.length > 0 ? (
          <Dropdown
            trigger={['hover']}
            menu={{
              items: options.map((option) => ({
                key: option.value,
                label: option.label,
              })),
              selectable: true,
              selectedKeys: symbol ? [symbol] : [],
              onClick: ({ key }) => {
                setFieldSymbols((prev) => ({ ...prev, [fieldKey]: key }));
                if (field.type == 'datetime') {
                  setFieldPickers((prev) => ({ ...prev, [fieldKey]: key }));
                }
              },
            }}
          >
            <Space size={4}>
              <span>{field.title}</span>
              <DownOutlined />
            </Space>
          </Dropdown>
        ) : (
          <span>{field.title}</span>
        )}
        <div style={{ flex: 1, minWidth: 0 }}>
          <WayTextBox
            options={field.type == 'datetime' ? { picker } : undefined}
            search={true}
            name={getSearchName(field)}
            attr={field}
            value={fieldValues[fieldKey]}
            onChange={(value) => {
              console.log(value);
              setFieldValues((prev) => ({ ...prev, [fieldKey]: value }));
            }}
            onSearchBefore={(item, callback) => {
              if (props.onSearchData) {
                props.onSearchData(item, (data) => {
                  callback(data.model, data);
                });
              }
            }}
            onSearchData={props.onSearchData}
          />
        </div>
      </div>
    );
  }

  function handleSearch() {
    const whereList: SearchWhere[] = [];
    searchFields.forEach((field) => {
      const fieldKey = getFieldKey(field);
      if (!fieldKey) return;
      const rawValue = fieldValues[fieldKey];
      const symbol = fieldSymbols[fieldKey] ?? getDefaultSymbol(field);
      const svalue = TypeDataToAny(field, rawValue);

      if (field.type == 'datetime' && isArray(svalue)) {
        if (svalue && svalue[0] != null) {
          whereList.push({ name: getSearchName(field), symbol: '>=', value: svalue[0] });
        }
        if (svalue && svalue[1] != null) {
          whereList.push({ name: getSearchName(field), symbol: '<=', value: svalue[1] });
        }
        return;
      }

      if (symbol == 'isnull' || symbol == 'isnotnull') {
        whereList.push({ name: getSearchName(field), symbol, value: '' });
        return;
      }

      if (svalue == undefined || svalue === '' || svalue === null) return;

      whereList.push({ name: getSearchName(field), symbol, value: svalue });
    });

    if (whereList.length == 0) {
      props.onSearch();
      return;
    }
    props.onSearch(whereList);
  }

  function handleReset() {
    setFieldValues({});
    setFieldSymbols({});
    setFieldPickers({});
    if (props.onReset) {
      props.onReset();
      return;
    }
    props.onSearch();
  }

  const fieldRows = useMemo(() => {
    const rows: WayFieldAttribute[][] = [];
    searchFields.forEach((field, index) => {
      const rowIndex = Math.floor(index / 4);
      if (!rows[rowIndex]) rows[rowIndex] = [];
      rows[rowIndex].push(field);
    });
    return rows;
  }, [searchFields]);

  const hasFields = fieldRows.length > 0;
  const lastRow = hasFields ? fieldRows[fieldRows.length - 1] : [];
  const canPlaceButtonInline = hasFields && lastRow.length < 4;

  return (
    <Space direction="vertical" size={8} style={{ width: '100%' }}>
      {fieldRows.map((row, rowIndex) => {
        const isLast = rowIndex == fieldRows.length - 1;
        const showInlineButton = isLast && row.length < 4;
        return (
          <Row key={`row-${rowIndex}-${props.resetKey ?? 0}`} gutter={[24, 8]}>
            {row.map((field) => {
              const fieldKey = getFieldKey(field);
              return (
                <Col key={`${fieldKey}-${props.resetKey ?? 0}`} span={6}>
                  {renderFieldItem(field)}
                </Col>
              );
            })}
            {showInlineButton ? (
              <Flex flex={1} justify="end" gap={8} style={{ marginRight: '12px' }}>
                <Button style={{ width: '70px' }} icon={<RollbackOutlined />} onClick={handleReset}>
                  重置
                </Button>
                <Button
                  style={{ width: '70px' }}
                  type={'primary'}
                  icon={<SearchOutlined />}
                  onClick={handleSearch}
                >
                  搜索
                </Button>
              </Flex>
            ) : null}
          </Row>
        );
      })}
      {!canPlaceButtonInline ? (
        <Flex flex={1} justify="end" gap={8}>
          <Button style={{ width: '70px' }} icon={<RollbackOutlined />} onClick={handleReset}>
            重置
          </Button>
          <Button
            style={{ width: '70px' }}
            type={'primary'}
            icon={<SearchOutlined />}
            onClick={handleSearch}
          >
            搜索
          </Button>
        </Flex>
      ) : null}
    </Space>
  );
};

export default WayProSearch;
